:root{
  --primary: cyan;
  --secondary: lime;
  --dark: #131a26;
  --light: #eee;
}

*{
  margin: 1.5px;
  padding: 1.5px;
  box-sizing: border-box;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;

}

body{
  background-color: var(--light);
}

.App{
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;

}

.calculator {
  width: 100%;
  max-width: 400px;
  background-color: #FFF;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0px 2px 64px rgba(0, 0, 0, 0.2);
}

.display {
  padding: 16px;
  text-align: right;
  background-color: red;
  color: yellowgreen;
  font-size: 24px;
  font-weight: 300;
}

.display span {
  font-size: 14px;
  color: yellow;
}

button {
  appearance: none;
  border: solid;
  outline: auto;
  color: black;
  border-color: black;
  font-size: 20px;
  padding: 16px;
  cursor: pointer;
  transition: 0.4s;
}

button:hover{
  opacity: 0.9;
}

.operators {
  display: flex;
}

.operators button {
  flex:1 1 0%;
  background-color: var(--primary);
  font-weight: 1000;
}

.digits {
  display: flex;
  flex-wrap: wrap;
}

.digits button{
  flex: 1 1 30%;
  max-width: 33.33%;
  background-color: var(--secondary);
}
